<template>
  <div class="resource">
    <div class="page-header">
      <div class="header-content">
        <h1> </h1>
        <p>,      .</p>
      </div>
      <div class="header-actions">
        <el-button type="primary" @click="showReservation = true">
          <el-icon><Calendar /></el-icon>
          
        </el-button>
        <el-button @click="showMyReservations = true">
          <el-icon><List /></el-icon>
           
        </el-button>
      </div>
    </div>

    <!--    -->
    <div class="resource-cards">
      <div class="resource-card">
        <div class="card-icon">
          <el-icon><OfficeBuilding /></el-icon>
        </div>
        <div class="card-content">
          <div class="card-title"></div>
          <div class="card-value">{{ meetingRooms.length }}</div>
          <div class="card-subtitle"> </div>
        </div>
      </div>
      
      <div class="resource-card">
        <div class="card-icon">
          <el-icon><Van /></el-icon>
        </div>
        <div class="card-content">
          <div class="card-title"> </div>
          <div class="card-value">{{ vehicles.length }}</div>
          <div class="card-subtitle"> </div>
        </div>
      </div>
      
      <div class="resource-card">
        <div class="card-icon">
          <el-icon><Clock /></el-icon>
        </div>
        <div class="card-content">
          <div class="card-title"> </div>
          <div class="card-value">{{ todayReservations }}</div>
          <div class="card-subtitle"></div>
        </div>
      </div>
      
      <div class="resource-card">
        <div class="card-icon">
          <el-icon><User /></el-icon>
        </div>
        <div class="card-content">
          <div class="card-title"> </div>
          <div class="card-value">{{ activeUsers }}</div>
          <div class="card-subtitle"></div>
        </div>
      </div>
    </div>

    <!--   -->
    <div class="resource-section">
      <div class="section-header">
        <h3> </h3>
        <div class="filter-options">
          <el-select v-model="selectedType" placeholder=" " style="width: 150px">
            <el-option label="" value="" />
            <el-option label="" value="meeting" />
            <el-option label="" value="vehicle" />
            <el-option label="" value="other" />
          </el-select>
          <el-date-picker
            v-model="selectedDate"
            type="date"
            placeholder=" "
            format="YYYY-MM-DD"
            value-format="YYYY-MM-DD"
          />
        </div>
      </div>
      
      <div class="resource-list">
        <div class="resource-item" v-for="resource in filteredResources" :key="resource.id">
          <div class="resource-info">
            <div class="resource-header">
              <div class="resource-name">{{ resource.name }}</div>
              <div class="resource-meta">
                <el-tag :type="getResourceType(resource.type)" size="small">
                  {{ getResourceTypeName(resource.type) }}
                </el-tag>
                <span class="resource-location">{{ resource.location }}</span>
              </div>
            </div>
            <div class="resource-details">
              <div class="resource-capacity">
                <el-icon><User /></el-icon>
                <span>{{ resource.capacity }}</span>
              </div>
              <div class="resource-equipment" v-if="resource.equipment">
                <el-icon><Setting /></el-icon>
                <span>{{ resource.equipment }}</span>
              </div>
              <div class="resource-status" :class="resource.status">
                {{ getStatusText(resource.status) }}
              </div>
            </div>
            <div class="resource-description">{{ resource.description }}</div>
          </div>
          <div class="resource-actions">
            <el-button 
              type="primary" 
              @click="reserveResource(resource)"
              :disabled="resource.status === 'occupied'"
            >
              <el-icon><Calendar /></el-icon>
              
            </el-button>
            <el-button @click="viewResourceDetails(resource)">
              <el-icon><View /></el-icon>
              
            </el-button>
          </div>
        </div>
      </div>
    </div>

    <!--   -->
    <el-dialog
      v-model="showReservation"
      title=" "
      width="600px"
    >
      <el-form :model="reservationForm" label-width="80px">
        <el-form-item label=" ">
          <el-select v-model="reservationForm.resourceId" placeholder=" ">
            <el-option
              v-for="resource in availableResources"
              :key="resource.id"
              :label="resource.name"
              :value="resource.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label=" ">
          <el-date-picker
            v-model="reservationForm.date"
            type="date"
            placeholder=" "
            format="YYYY-MM-DD"
            value-format="YYYY-MM-DD"
          />
        </el-form-item>
        <el-form-item label=" ">
          <el-time-picker
            v-model="reservationForm.startTime"
            placeholder=" "
            format="HH:mm"
            value-format="HH:mm"
          />
        </el-form-item>
        <el-form-item label=" ">
          <el-time-picker
            v-model="reservationForm.endTime"
            placeholder=" "
            format="HH:mm"
            value-format="HH:mm"
          />
        </el-form-item>
        <el-form-item label=" ">
          <el-input
            v-model="reservationForm.purpose"
            placeholder="  "
          />
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="reservationForm.attendees"
            placeholder="  "
            type="number"
          />
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="reservationForm.notes"
            type="textarea"
            placeholder="  "
            :rows="3"
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="showReservation = false"></el-button>
        <el-button type="primary" @click="submitReservation"></el-button>
      </template>
    </el-dialog>

    <!--    -->
    <el-dialog
      v-model="showMyReservations"
      title="  "
      width="800px"
    >
      <div class="my-reservations">
        <div class="reservation-list">
          <div class="reservation-item" v-for="reservation in myReservations" :key="reservation.id">
            <div class="reservation-info">
              <div class="reservation-header">
                <div class="reservation-title">{{ reservation.resourceName }}</div>
                <div class="reservation-meta">
                  <el-tag :type="getReservationStatusType(reservation.status)" size="small">
                    {{ reservation.status }}
                  </el-tag>
                  <span class="reservation-date">{{ reservation.date }}</span>
                </div>
              </div>
              <div class="reservation-details">
                <div class="reservation-time">
                  <el-icon><Clock /></el-icon>
                  <span>{{ reservation.startTime }} - {{ reservation.endTime }}</span>
                </div>
                <div class="reservation-purpose">
                  <el-icon><Document /></el-icon>
                  <span>{{ reservation.purpose }}</span>
                </div>
                <div class="reservation-attendees">
                  <el-icon><User /></el-icon>
                  <span>{{ reservation.attendees }}</span>
                </div>
              </div>
            </div>
            <div class="reservation-actions">
              <el-button @click="editReservation(reservation)">
                <el-icon><Edit /></el-icon>
                
              </el-button>
              <el-button @click="cancelReservation(reservation)">
                <el-icon><Close /></el-icon>
                
              </el-button>
            </div>
          </div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'ResourcePage',
  data() {
    return {
      selectedType: '',
      selectedDate: '',
      showReservation: false,
      showMyReservations: false,
      reservationForm: {
        resourceId: '',
        date: '',
        startTime: '',
        endTime: '',
        purpose: '',
        attendees: '',
        notes: ''
      },
      todayReservations: 8,
      activeUsers: 15,
      meetingRooms: [
        { id: 1, name: '', capacity: 20, location: '3' },
        { id: 2, name: ' A', capacity: 8, location: '2' },
        { id: 3, name: ' B', capacity: 6, location: '2' }
      ],
      vehicles: [
        { id: 4, name: '  1', capacity: 4, location: '' },
        { id: 5, name: '  2', capacity: 7, location: '' }
      ],
      resources: [
        {
          id: 1,
          name: '',
          type: 'meeting',
          location: '3',
          capacity: 20,
          equipment: ', , ',
          status: 'available',
          description: '    '
        },
        {
          id: 2,
          name: ' A',
          type: 'meeting',
          location: '2',
          capacity: 8,
          equipment: ', ',
          status: 'occupied',
          description: '   '
        },
        {
          id: 3,
          name: ' B',
          type: 'meeting',
          location: '2',
          capacity: 6,
          equipment: '',
          status: 'available',
          description: '   '
        },
        {
          id: 4,
          name: '  1',
          type: 'vehicle',
          location: '',
          capacity: 4,
          equipment: 'GPS, ',
          status: 'available',
          description: '     '
        },
        {
          id: 5,
          name: '  2',
          type: 'vehicle',
          location: '',
          capacity: 7,
          equipment: 'GPS, ',
          status: 'occupied',
          description: '   '
        }
      ],
      myReservations: [
        {
          id: 1,
          resourceName: '',
          date: '2024-01-20',
          startTime: '14:00',
          endTime: '16:00',
          purpose: '  ',
          attendees: 8,
          status: ''
        },
        {
          id: 2,
          resourceName: '  1',
          date: '2024-01-22',
          startTime: '09:00',
          endTime: '18:00',
          purpose: ' ',
          attendees: 2,
          status: ''
        }
      ]
    }
  },
  computed: {
    filteredResources() {
      let filtered = this.resources
      
      if (this.selectedType) {
        filtered = filtered.filter(resource => resource.type === this.selectedType)
      }
      
      return filtered
    },
    availableResources() {
      return this.resources.filter(resource => resource.status === 'available')
    }
  },
  methods: {
    getResourceType(type) {
      const typeMap = {
        'meeting': 'primary',
        'vehicle': 'success',
        'other': 'info'
      }
      return typeMap[type] || 'info'
    },
    getResourceTypeName(type) {
      const typeMap = {
        'meeting': '',
        'vehicle': '',
        'other': ''
      }
      return typeMap[type] || ''
    },
    getStatusText(status) {
      const statusMap = {
        'available': ' ',
        'occupied': ' ',
        'maintenance': ' '
      }
      return statusMap[status] || '  '
    },
    getReservationStatusType(status) {
      const statusMap = {
        '': 'success',
        '': 'danger',
        '': 'info'
      }
      return statusMap[status] || 'info'
    },
    reserveResource(resource) {
      this.reservationForm.resourceId = resource.id
      this.showReservation = true
    },
    viewResourceDetails(resource) {
      this.$message.info(`${resource.name}  `)
    },
    submitReservation() {
      this.$message.success(' .')
      this.showReservation = false
      this.reservationForm = {
        resourceId: '',
        date: '',
        startTime: '',
        endTime: '',
        purpose: '',
        attendees: '',
        notes: ''
      }
    },
    editReservation() {
      this.$message.info(' ')
    },
    cancelReservation() {
      this.$confirm('  ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message.success(' .')
      })
    }
  }
}
</script>

<style scoped>
