<template>
  <div class="attendance">
    <div class="page-header">
      <div class="header-content">
        <h1> </h1>
        <p>    .</p>
      </div>
      <div class="header-actions">
        <el-button type="primary" @click="checkInOut">
          <el-icon><Clock /></el-icon>
          {{ isCheckedIn ? ' ' : ' ' }}
        </el-button>
        <el-button @click="showVacationRequest = true">
          <el-icon><Calendar /></el-icon>
           
        </el-button>
      </div>
    </div>

    <!--    -->
    <div class="attendance-cards">
      <div class="attendance-card">
        <div class="card-icon">
          <el-icon><Clock /></el-icon>
        </div>
        <div class="card-content">
          <div class="card-title"> </div>
          <div class="card-value">{{ todayWorkTime }}</div>
          <div class="card-subtitle">{{ isCheckedIn ? ' ' : '' }}</div>
        </div>
      </div>
      
      <div class="attendance-card">
        <div class="card-icon">
          <el-icon><Calendar /></el-icon>
        </div>
        <div class="card-content">
          <div class="card-title">  </div>
          <div class="card-value">{{ monthlyWorkDays }}</div>
          <div class="card-subtitle">: 22</div>
        </div>
      </div>
      
      <div class="attendance-card">
        <div class="card-icon">
          <el-icon><Sunny /></el-icon>
        </div>
        <div class="card-content">
          <div class="card-title"> </div>
          <div class="card-value">{{ remainingVacation }}</div>
          <div class="card-subtitle"> 15 </div>
        </div>
      </div>
      
      <div class="attendance-card">
        <div class="card-icon">
          <el-icon><TrendCharts /></el-icon>
        </div>
        <div class="card-content">
          <div class="card-title"></div>
          <div class="card-value">{{ attendanceRate }}%</div>
          <div class="card-subtitle"> </div>
        </div>
      </div>
    </div>

    <!--   -->
    <div class="attendance-tabs">
      <el-tabs v-model="activeTab" @tab-change="handleTabChange">
        <el-tab-pane label=" " name="records">
          <div class="records-section">
            <div class="section-header">
              <h3> </h3>
              <div class="filter-options">
                <el-date-picker
                  v-model="dateRange"
                  type="daterange"
                  range-separator="~"
                  start-placeholder=""
                  end-placeholder=""
                  format="YYYY-MM-DD"
                  value-format="YYYY-MM-DD"
                />
                <el-select v-model="selectedEmployee" placeholder=" " style="width: 150px">
                  <el-option label="" value="" />
                  <el-option label="" value="" />
                  <el-option label="" value="" />
                  <el-option label="" value="" />
                </el-select>
              </div>
            </div>
            
            <div class="records-table">
              <el-table :data="filteredRecords" style="width: 100%">
                <el-table-column prop="date" label="" width="120" />
                <el-table-column prop="employee" label="" width="100" />
                <el-table-column prop="checkIn" label="" width="100" />
                <el-table-column prop="checkOut" label="" width="100" />
                <el-table-column prop="workTime" label="" width="100" />
                <el-table-column prop="overtime" label="" width="100" />
                <el-table-column prop="status" label="" width="100">
                  <template #default="scope">
                    <el-tag :type="getStatusType(scope.row.status)" size="small">
                      {{ scope.row.status }}
                    </el-tag>
                  </template>
                </el-table-column>
                <el-table-column label="" width="120">
                  <template #default="scope">
                    <el-button type="text" size="small" @click="editRecord(scope.row)">
                      <el-icon><Edit /></el-icon>
                    </el-button>
                    <el-button type="text" size="small" @click="deleteRecord(scope.row)">
                      <el-icon><Delete /></el-icon>
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
        </el-tab-pane>
        
        <el-tab-pane label=" " name="vacation">
          <div class="vacation-section">
            <div class="section-header">
              <h3>  </h3>
              <el-button type="primary" @click="showVacationRequest = true">
                <el-icon><Plus /></el-icon>
                 
              </el-button>
            </div>
            
            <div class="vacation-list">
              <div class="vacation-item" v-for="vacation in vacationRequests" :key="vacation.id">
                <div class="vacation-info">
                  <div class="vacation-dates">
                    <span class="start-date">{{ vacation.startDate }}</span>
                    <span class="separator">~</span>
                    <span class="end-date">{{ vacation.endDate }}</span>
                    <span class="duration">({{ vacation.days }})</span>
                  </div>
                  <div class="vacation-reason">{{ vacation.reason }}</div>
                  <div class="vacation-employee">{{ vacation.employee }}</div>
                </div>
                <div class="vacation-status">
                  <el-tag :type="getVacationStatusType(vacation.status)" size="small">
                    {{ vacation.status }}
                  </el-tag>
                  <div class="vacation-actions" v-if="vacation.status === ''">
                    <el-button type="text" size="small" @click="approveVacation(vacation)">
                      
                    </el-button>
                    <el-button type="text" size="small" @click="rejectVacation(vacation)">
                      
                    </el-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </el-tab-pane>
        
        <el-tab-pane label=" " name="statistics">
          <div class="statistics-section">
            <div class="stats-grid">
              <div class="stat-item">
                <div class="stat-title">   </div>
                <div class="stat-value">{{ monthlyWorkHours }}</div>
                <div class="stat-change positive">+2.5</div>
              </div>
              <div class="stat-item">
                <div class="stat-title">  </div>
                <div class="stat-value">{{ averageCheckIn }}</div>
                <div class="stat-change negative">-5</div>
              </div>
              <div class="stat-item">
                <div class="stat-title">  </div>
                <div class="stat-value">{{ averageCheckOut }}</div>
                <div class="stat-change positive">+10</div>
              </div>
              <div class="stat-item">
                <div class="stat-title"> </div>
                <div class="stat-value">{{ overtimeHours }}</div>
                <div class="stat-change neutral">0</div>
              </div>
            </div>
            
            <div class="chart-section">
              <h4>  </h4>
              <div class="chart-placeholder">
                <el-icon><TrendCharts /></el-icon>
                <p>  </p>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>

    <!--    -->
    <el-dialog
      v-model="showVacationRequest"
      title=" "
      width="500px"
    >
      <el-form :model="vacationForm" label-width="80px">
        <el-form-item label=" ">
          <el-select v-model="vacationForm.type" placeholder="  ">
            <el-option label="" value="annual" />
            <el-option label="" value="sick" />
            <el-option label="" value="family" />
            <el-option label="" value="other" />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="vacationForm.startDate"
            type="date"
            placeholder=" "
            format="YYYY-MM-DD"
            value-format="YYYY-MM-DD"
          />
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="vacationForm.endDate"
            type="date"
            placeholder=" "
            format="YYYY-MM-DD"
            value-format="YYYY-MM-DD"
          />
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="vacationForm.reason"
            type="textarea"
            placeholder="  "
            :rows="3"
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="showVacationRequest = false"></el-button>
        <el-button type="primary" @click="submitVacationRequest"></el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'AttendancePage',
  data() {
    return {
      activeTab: 'records',
      isCheckedIn: false,
      checkInTime: null,
      checkOutTime: null,
      showVacationRequest: false,
      dateRange: [],
      selectedEmployee: '',
      vacationForm: {
        type: '',
        startDate: '',
        endDate: '',
        reason: ''
      },
      todayWorkTime: '0 0',
      monthlyWorkDays: 18,
      remainingVacation: 10,
      attendanceRate: 95,
      monthlyWorkHours: 144,
      averageCheckIn: '09:15',
      averageCheckOut: '18:30',
      overtimeHours: 8,
      records: [
        {
          id: 1,
          date: '2024-01-15',
          employee: '',
          checkIn: '09:15',
          checkOut: '18:30',
          workTime: '8 15',
          overtime: '0 15',
          status: ''
        },
        {
          id: 2,
          date: '2024-01-14',
          employee: '',
          checkIn: '09:00',
          checkOut: '19:00',
          workTime: '9 0',
          overtime: '1 0',
          status: ''
        },
        {
          id: 3,
          date: '2024-01-13',
          employee: '',
          checkIn: '09:30',
          checkOut: '18:00',
          workTime: '7 30',
          overtime: '0 0',
          status: ''
        }
      ],
      vacationRequests: [
        {
          id: 1,
          employee: '',
          startDate: '2024-01-20',
          endDate: '2024-01-22',
          days: 3,
          reason: ' ',
          status: ''
        },
        {
          id: 2,
          employee: '',
          startDate: '2024-01-25',
          endDate: '2024-01-25',
          days: 1,
          reason: ' ',
          status: ''
        },
        {
          id: 3,
          employee: '',
          startDate: '2024-01-18',
          endDate: '2024-01-19',
          days: 2,
          reason: '',
          status: ''
        }
      ]
    }
  },
  computed: {
    filteredRecords() {
      let filtered = this.records
      
      if (this.selectedEmployee) {
        filtered = filtered.filter(record => record.employee === this.selectedEmployee)
      }
      
      return filtered
    }
  },
  methods: {
    checkInOut() {
      const now = new Date()
      const timeString = now.toTimeString().slice(0, 5)
      
      if (!this.isCheckedIn) {
        this.isCheckedIn = true
        this.checkInTime = timeString
        this.$message.success(`  : ${timeString}`)
      } else {
        this.isCheckedIn = false
        this.checkOutTime = timeString
        this.$message.success(`  : ${timeString}`)
      }
    },
    handleTabChange(tab) {
      this.activeTab = tab
    },
    getStatusType(status) {
      const statusMap = {
        '': 'success',
        '': 'warning',
        '': 'danger',
        '': 'info'
      }
      return statusMap[status] || 'info'
    },
    getVacationStatusType(status) {
      const statusMap = {
        '': 'warning',
        '': 'success',
        '': 'danger'
      }
      return statusMap[status] || 'info'
    },
    editRecord(rec) {
      this.$message.info(`${rec.date}   .`)
    },
    deleteRecord() {
      this.$confirm(' ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message.success('.')
      })
    },
    approveVacation(vacation) {
      vacation.status = ''
      this.$message.success(' .')
    },
    rejectVacation(vacation) {
      vacation.status = ''
      this.$message.info(' .')
    },
    submitVacationRequest() {
      this.$message.success('  .')
      this.showVacationRequest = false
      this.vacationForm = {
        type: '',
        startDate: '',
        endDate: '',
        reason: ''
      }
    }
  }
}
</script>

<style scoped>
.attendance {
  max-width: 1200px;
  margin: 0 auto;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 24px;
}

.header-content h1 {
  font-size: 32px;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 8px;
}

.header-content p {
  font-size: 16px;
  color: #606266;
  margin: 0;
}

.header-actions {
  display: flex;
  gap: 12px;
}

.attendance-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 24px;
}

.attendance-card {
  background: white;
  padding: 24px;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  gap: 16px;
  transition: transform 0.3s ease;
}

.attendance-card:hover {
  transform: translateY(-2px);
}

.card-icon {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: white;
  background: #4f46e5;
}

.card-content {
  flex: 1;
}

.card-title {
  font-size: 14px;
  color: #606266;
  margin-bottom: 4px;
}

.card-value {
  font-size: 24px;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 4px;
}

.card-subtitle {
  font-size: 12px;
  color: #909399;
}

.attendance-tabs {
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.records-section, .vacation-section, .statistics-section {
  padding: 24px;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.section-header h3 {
  font-size: 18px;
  font-weight: 600;
  color: #2c3e50;
  margin: 0;
}

.filter-options {
  display: flex;
  gap: 12px;
  align-items: center;
}

.records-table {
  margin-top: 20px;
}

.vacation-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.vacation-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e9ecef;
}

.vacation-info {
  flex: 1;
}

.vacation-dates {
  font-weight: 500;
  color: #2c3e50;
  margin-bottom: 4px;
}

.start-date, .end-date {
  font-size: 14px;
}

.separator {
  margin: 0 4px;
  color: #909399;
}

.duration {
  color: #667eea;
  font-weight: 500;
}

.vacation-reason {
  font-size: 14px;
  color: #606266;
  margin-bottom: 4px;
}

.vacation-employee {
  font-size: 12px;
  color: #909399;
}

.vacation-status {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 8px;
}

.vacation-actions {
  display: flex;
  gap: 4px;
}

.statistics-section {
  padding: 24px;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 32px;
}

.stat-item {
  background: #f8f9fa;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
}

.stat-title {
  font-size: 14px;
  color: #606266;
  margin-bottom: 8px;
}

.stat-value {
  font-size: 24px;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 4px;
}

.stat-change {
  font-size: 12px;
  font-weight: 500;
}

.stat-change.positive {
  color: #67c23a;
}

.stat-change.negative {
  color: #f56c6c;
}

.stat-change.neutral {
  color: #909399;
}

.chart-section {
  margin-top: 32px;
}

.chart-section h4 {
  font-size: 16px;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 16px;
}

.chart-placeholder {
  height: 200px;
  background: #f8f9fa;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #909399;
}

.chart-placeholder .el-icon {
  font-size: 32px;
  margin-bottom: 8px;
}
</style>
